const baseUrl = 'https://mall.program66.com'
const httpRequest = (opts, data) => {
	let httpDefaultOpts = {
		url: baseUrl + opts.url,
		data: data,
		method: opts.method,
		header: opts.method == 'get' ? {
			'X-Requested-With': 'XMLHttpRequest',
			"Accept": "application/json",
			'Authorization': 'Basic YWRtaW46YWRtaW4=',
			"Content-Type": "application/json; charset=UTF-8"
		} : {
			'X-Requested-With': 'XMLHttpRequest',
			'Authorization': 'Basic YWRtaW46YWRtaW4=',
			'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
		},
		dataType: 'json',
	}

	let promise = new Promise(function(resolve, reject) {
		uni.showLoading({
			title: ''
		});
		uni.request(httpDefaultOpts).then(
			(res) => {
				resolve(res[1])
			}
		).catch(
			// setTimeout((response) => {
			//     reject(response)
			// },0)
			(response) => {
				reject(response)
			}
		)
	})
	setTimeout(function() {
		uni.hideLoading();
	}, 2000);
	promise.then(() => {}).catch(() => {})
	return promise
};
//Token
const httpTokenRequest = (opts, data) => {
	const token = uni.getStorageSync('token');
	//tokenstorage
	let httpDefaultOpts = {
		url: baseUrl + opts.url,
		data: data,
		method: opts.method,
		header: opts.method == 'get' ? {
			'X-Requested-With': 'XMLHttpRequest',
			'Authorization': "Bearer " + token,
			'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
		} : {
			'X-Requested-With': 'XMLHttpRequest',
			'Authorization': "Bearer " + token,
			"Accept": "application/json",
			"Content-Type": "application/json; charset=UTF-8"
		},
		dataType: 'json',
	}
	let promise = new Promise(function(resolve, reject) {
		uni.showLoading({
			title: ''
		});
		uni.request(httpDefaultOpts).then(
			(res) => {
				resolve(res[1])
			}
		).catch(
			// setTimeout((response) => {
			//     reject(response)
			// },0)
			(response) => {
				reject(response)
			}
		)
	})
	setTimeout(function() {
		uni.hideLoading();
	}, 1000);
	promise.then(() => {}).catch(() => {})
	return promise
};
